import React from 'react';
import clsx from 'clsx';
import '../../css/utilfooter.css'
import { Link } from 'react-router-dom'
import { Grid } from '@material-ui/core';
import List from '@material-ui/core/List';
import Button from '@material-ui/core/Button';
import Drawer from '@material-ui/core/Drawer';
import HomeIcon from '@material-ui/icons/Home';
import PetsIcon from '@material-ui/icons/Pets';
import CallIcon from '@material-ui/icons/Call';
import Divider from '@material-ui/core/Divider';
import GroupIcon from '@material-ui/icons/Group';
import EmailIcon from '@material-ui/icons/Email';
import ListItem from '@material-ui/core/ListItem';
import TwitterIcon from '@material-ui/icons/Twitter';
import Typography from '@material-ui/core/Typography';
import { makeStyles } from '@material-ui/core/styles';
import FacebookIcon from '@material-ui/icons/Facebook';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import LocationOnIcon from '@material-ui/icons/LocationOn';
import ArrowRightIcon from '@material-ui/icons/ArrowRight';
import ChromeReaderModeIcon from '@material-ui/icons/ChromeReaderMode';
import SignalCellular4BarIcon from '@material-ui/icons/SignalCellular4Bar';

const useStyles = makeStyles((theme) => ({
    list: {
        width: 250,
    },
    fullList: {
        width: 'auto',
    },
    drawerText: {
        display: 'flex',
        alignItems: 'center',
        padding: theme.spacing(0, 3),
        // necessary for content to be below app bar
        ...theme.mixins.toolbar,
    },
}));

export default function TemporaryDrawer() {
    const classes = useStyles();
    const [state, setState] = React.useState({
        bottom: false,
    });

    const toggleDrawer = (anchor, open) => (event) => {
        if (event.type === 'keydown' && (event.key === 'Tab' || event.key === 'Shift')) {
            return;
        }

            setState({ ...state, [anchor]: open });
    };

    const list = (anchor) => (
        <div
                className={clsx(classes.list, {
                [classes.fullList]: anchor === 'bottom',
            })}
            role="presentation"
            onClick={toggleDrawer(anchor, false)}
            onKeyDown={toggleDrawer(anchor, false)}
        >
            <Grid
                direction="row"
                justify="space-evenly"
                alignItems="center"
                container
            >
                <Grid item>
                    <List>
                        <Divider />
                        <div className={classes.drawerText}>
                            <span style={{ fontSize: 18}}>Locate/Contact</span>
                        </div>
                        <Divider />
                        <List>
                            <ListItem component={Link} to="/" button>
                                <ListItemIcon> <HomeIcon /> </ListItemIcon>
                                <ListItemText>Home</ListItemText>
                            </ListItem>
                        </List>
                        <List>
                            <ListItem component={Link} to="/service" button>
                                <ListItemIcon> <SignalCellular4BarIcon  /> </ListItemIcon>
                                <ListItemText>Services</ListItemText>
                            </ListItem>
                        </List>
                        <List>
                            <ListItem component={Link} to="/aboutUs" button>
                                <ListItemIcon> <GroupIcon /> </ListItemIcon>
                                <ListItemText>About Us</ListItemText>
                            </ListItem>
                        </List>
                        <List>
                            <ListItem component={Link} to="/careers" button>
                                <ListItemIcon> <PetsIcon /> </ListItemIcon>
                                <ListItemText>Careers</ListItemText>
                            </ListItem>
                        </List>
                        <List>
                            <ListItem component={Link} to="/publications" button>
                                <ListItemIcon> <ChromeReaderModeIcon /> </ListItemIcon>
                                <ListItemText>Publications</ListItemText>
                            </ListItem>
                        </List>
                    </List>
                </Grid>
                <Grid item />
                <Grid item>
                    <List>
                        <Divider />
                        <div className={classes.drawerText}>
                            <span style={{ fontSize: 18}}>Locate/Contact</span>
                        </div>
                        <Divider />
                        <a href="6" className="tel">
                            <List>
                                <ListItem component={Link} button>
                                    <ListItemIcon> <LocationOnIcon /> </ListItemIcon>
                                    <ListItemText> <span>Home</span> </ListItemText>
                                </ListItem>
                            </List>
                        </a>
                        <a href="tel:+233302817993" className="tel">
                            <List>
                                <ListItem component={Link} button>
                                    <ListItemIcon> <CallIcon /> </ListItemIcon>
                                    <ListItemText><span>(+233) 302 817 993</span></ListItemText>
                                </ListItem>
                            </List>
                        </a>
                        <a href="3" className="email">
                            <List>
                                <ListItem component="href" to="tel:+233302817993" button>
                                    <ListItemIcon> <EmailIcon /> </ListItemIcon>
                                    <ListItemText><span>info@virtualpointassociates.com</span></ListItemText>
                                </ListItem>
                            </List>
                        </a>
                        <a href="3" className="twitter">
                            <List>
                                <ListItem button>
                                    <ListItemIcon> <TwitterIcon /> </ListItemIcon>
                                    <ListItemText><span>@Twitter</span></ListItemText>
                                </ListItem>
                            </List>
                        </a>
                        <a href="no" className="facebook" >
                            <List>
                                <ListItem component={Link} button>
                                    <ListItemIcon> <FacebookIcon /> </ListItemIcon>
                                    <ListItemText><span>@Facebook</span></ListItemText>
                                </ListItem>
                            </List>
                        </a>
                    </List>
                </Grid>
            </Grid>
            <Divider />
        </div>
    );

    return (
        <div>
            {[`bottom`].map((anchor) => (
                <React.Fragment key={anchor}>
                    <Typography className="buttonfooter">
                        <Button
                            onClick={toggleDrawer(anchor, true)}
                        >
                            Click for More<ArrowRightIcon /></Button>
                        <Drawer anchor={anchor} open={state[anchor]} onClose={toggleDrawer(anchor, false)}>
                            {list(anchor)}
                        </Drawer>
                    </Typography>
                </React.Fragment>
            ))}
        </div>
    );
}