import React, { Component, Fragment } from "react";
import clsx from 'clsx';
import PropTypes from 'prop-types'
// Util
//import File from "../../util/file";
import FileUpload from "../../util/fileUpload";
// Redux'
//import { connect } from 'react-redux'
//import { postScream } from '../../redux/action/dataAction'
// MUI stuff
import Button from '@material-ui/core/Button';
//import SendIcon from '@material-ui/icons/Send';
import CallIcon from '@material-ui/icons/Call';
import EmailIcon from '@material-ui/icons/Email';
import PersonIcon from '@material-ui/icons/Person'
import FormControl from '@material-ui/core/FormControl';
import OutlinedInput from '@material-ui/core/OutlinedInput';
import InputAdornment from '@material-ui/core/InputAdornment';
import CreateIcon from '@material-ui/icons/Create';
import { withStyles, Container, Grid, IconButton } from "@material-ui/core";
import '../../css/careers.css'

const styles = (theme) => ({
    root: {
        display: 'flex',
        flexWrap: 'wrap',
        '& .MuiTextField-root': {
            margin: theme.spacing(1),
            width: 200,
        },
    },
    car: {
        marginTop: 200
    },
    grid: {
        display: 'flex',
        flexWrap: 'wrap',
    },
    margin: {
        margin: theme.spacing(1),
    },
    withoutLabel: {
        marginTop: theme.spacing(3),
    },
    textarea: {
        margin: '8',
        borderRadius: 8,
        alignItems: 'center',
        lineHeight: '1.1876em',
        letterSpacing: '0.00938em',
    },
    progressSpinner: {
        position: 'absolute',
    },
    button: {
        position: 'relative',
        textAlign: "center",
        padding: 30
    },
})
class careers extends Component {
    constructor(props){
        super(props);
        this.state = {
            firstName: "",
            lastName: "",
            phoneNumber: "",
            body: "",
            errors: {}
        }
    };
//    componentWillReceiveProps(nextProps) {
//        if (nextProps.UI.errors) {
//            this.setState({
//                errors: nextProps.UI.errors
//            })
//        }
//        if (!nextProps.UI.errors && !nextProps.UI.loading) {
//            this.setState({
//                firstName: "",
//                lastName: "",
//                phoneNumber: "",
//                body: "",
//                errors: {}
//            })
//        }
//    }
    handleChange = (event) => {
        this.setState({
            [event.target.name]: event.target.value
        });
    };
    handleSubmit = (event) => {
        event.preventDefault();
        console.log("hello");
//        const newScream = {
//            firstName: this.state.firstName,
//            lastName: this.state.lastName,
//            phoneNumber: this.state.phoneNumber,
//            body: this.state.body,
//        }
//        , UI: {loading} this.props.loginUser(newScream);
    };
    render() {
        const { errors } = this.state
        const { classes } = this.props
        return (
            <Fragment>
                <Container>
                <div className={classes.car}>
                    <Grid container>
                        <Grid item lg={1} sm={3}/>
                        <Grid item lg={10} sm={10} xs={12}>
                        <form noValidate autoComplete="off" onSubmit={this.handleSubmit}>
                            <Container>
                                <div spacing={2} style={{ textAlign: "center" }}>
                                    <Grid Container spacing={2} className={classes.grid}>
                                        <Grid spacing={2} item lg={6} sm={6} xs={12}>
                                            <FormControl  style={{ width: "90%"}} className={clsx(classes.margin, classes.textField)} variant="outlined">
                                                <OutlinedInput
                                                    name="firstName"
                                                    type="text"
                                                    error={errors.firstName ? true : false}
                                                    helperText={errors.firstName}
                                                    placeholder="First Name"
                                                    value={this.state.firstName}
                                                    onChange={this.handleChange}
                                                    startAdornment={<InputAdornment position="start"><IconButton><PersonIcon /></IconButton></InputAdornment>}
                                                    aria-describedby="outlined-weight-helper-text"
                                                    inputProps={{
                                                        'aria-label': 'weight',
                                                    }}
                                                    labelWidth={0}
                                                />
                                            </FormControl>
                                        </Grid>
                                        <Grid item lg={6} sm={6} xs={12}>
                                            <FormControl style={{ width: "90%"}} className={clsx(classes.margin, classes.textField)} variant="outlined">
                                                <OutlinedInput
                                                    name="lastName"
                                                    type="text"
                                                    error={errors.lastName ? true : false}
                                                    helperText={errors.lastName}
                                                    placeholder="Last Name"
                                                    value={this.state.lastName}
                                                    onChange={this.handleChange}
                                                    startAdornment={<InputAdornment position="start"><IconButton><PersonIcon /></IconButton></InputAdornment>}
                                                    aria-describedby="outlined-weight-helper-text"
                                                    inputProps={{
                                                        'aria-label': 'weight',
                                                    }}
                                                    labelWidth={0}
                                                />
                                            </FormControl>
                                        </Grid>
                                    </Grid>
                                </div>
                                <div spacing={2} style={{ textAlign: "center", marginTop: "40px" }}>
                                    <Grid Container spacing={2} className={classes.grid}>
                                        <Grid spacing={2} item lg={6} sm={6} xs={12}>
                                            <FormControl style={{ width: "90%"}} className={clsx(classes.margin, classes.textField)} variant="outlined">
                                                <OutlinedInput
                                                    id="email"
                                                    name="email"
                                                    type="email"
                                                    placeholder="Email"
                                                    error={errors.email ? true : false}
                                                    helperText={errors.email}
                                                    value={this.state.email}
                                                    onChange={this.handleChange}
                                                    startAdornment={<InputAdornment position="start"><IconButton><EmailIcon /></IconButton></InputAdornment>}
                                                    aria-describedby="outlined-weight-helper-text"
                                                    inputProps={{
                                                        'aria-label': 'weight',
                                                    }}
                                                    labelWidth={0}
                                                />
                                            </FormControl>
                                        </Grid>
                                        <Grid spacing={2} item lg={6} sm={6} xs={12}>
                                            <FormControl style={{ width: "90%"}} className={clsx(classes.margin, classes.textField)} variant="outlined">
                                                <OutlinedInput
                                                    id="phoneNumber"
                                                    name="phoneNumber"
                                                    type="tel"
                                                    error={errors.phoneNumber ? true : false}
                                                    helperText={errors.phoneNumber}
                                                    placeholder="Phone Numer"
                                                    value={this.state.phoneNumber}
                                                    onChange={this.handleChange}
                                                    startAdornment={<InputAdornment position="start"><IconButton><CallIcon /></IconButton></InputAdornment>}
                                                    aria-describedby="outlined-weight-helper-text"
                                                    inputProps={{
                                                        'aria-label': 'weight',
                                                    }}
                                                    labelWidth={0}
                                                />
                                            </FormControl>
                                        </Grid>
                                    </Grid>
                                </div>
                                <div style={{ textAlign: "center" }}>
                                <FormControl style={{ width: "96%"}} className={clsx(classes.margin, classes.textField)} variant="outlined">
                                    <OutlinedInput
                                        name="body"
                                        type="text"
                                        placeholder="Scream at your fellow apes"
                                        error={errors.body ? true : false}
                                        helperText={errors.body}
                                        value={this.state.body}
                                        onChange={this.handleChange}
                                        startAdornment={<InputAdornment position="start"><CreateIcon /></InputAdornment>}
                                        className={classes.textarea}
                                        style={{
                                            height: "175px",
                                            padding: "20px",
                                            fontSize: "16px",
                                            marginTop: "40px"
                                        }}
                                        multiline
                                        rows="3"
                                        fullWidth
                                    />
                                </FormControl>
                                </div>
                                {/* <div style={{ textAlign: "center" }}>
                                    <TextareaAutosize
                                        name="body"
                                        type="text"
                                        placeholder="Scream at your fellow apes"
                                        error={errors.body ? true : false}
                                        helperText={errors.body}
                                        value={this.state.body}
                                        onChange={this.handleChange}
                                        startAdornment={<InputAdornment position="start">First Name</InputAdornment>}
                                        className={classes.textarea}
                                        style={{
                                            height: "175px",
                                            fontFamily: "Roboto",
                                            padding: "20px",
                                            fontSize: "20px",
                                            margin: "8px !important",
                                            transition: "opacity 200ms cubic-bezier(0.4, 0, 0.2, 1) 0ms",
                                            marginTop: "40px"
                                        }}
                                        multiline
                                        rows="3"
                                        variant="outlined"
                                        fullWidth
                                        margin="normal"
                                        InputLabelProps={{
                                            shrink: true,
                                        }}
                                    />
                                </div> */}
                            <FileUpload />
                            {/*<File />*/}
                            <div className={ classes.button }>
                                <Button
                                    type = "submit"
                                    variant="contained"
                                    size="large"
                                    color="primary"
                                    className={classes.submitButton}
                                    style={{ padding : "10px", width: "24%", fontSize: "20px" }}
                                    /*endIcon={<Icon><SendIcon /></Icon>}*/
                                    /*disabled={loading}*/
                                >
                                    Send
                                    {/*{loading && (
                                        <CircularProgress size={20} className={classes.progressSpinner} />
                                    )}*/}
                                </Button>
                            </div>
                            </Container>
                        </form>
                        </Grid>
                        <Grid item lg={6} sm={3}/>
                    </Grid>
                </div>
                </Container>
                <br />
                <br />
            </Fragment>
        );
    }
}

careers.propTypes = {
    classes: PropTypes.object.isRequired,
    //postScream: PropTypes.func.isRequired,
    //UI: PropTypes.object.isRequired
}

//const mapStateToProps = state => ({
//    UI: state.UI
//});

//const mapActionsToProps = {
//    postScream
//}

export default withStyles(styles)(careers)
