import React, { Component } from 'react'
import { Parallax } from 'react-parallax';
import PropTypes from 'prop-types'
import { withStyles } from '@material-ui/core';

import companyStatement from '../assets/images/hardco.jpg'
import vpaCompany from '../assets/images/hardco.jpg'

const styles = (theme) => ({
    profile: {
        '& .image-wrapper': {
            textAlign: 'center',
            position: 'relative',
        },
        '& .profile-image': {
            width: 400,
            height: 200,
            objectFit: 'cover',
            maxWidth: '100%',
            borderRadius: '20%'
        },
    }
})
class VPACompany extends Component {
    render() {
        const { classes } = this.props;
        return(
            <div className={classes.profile}>
                <div className = "image-wrapper">
                    <Parallax
                        style={{top: "0"}}
                        className="profile-image"
                        blur={{ min: -30, max: 15 }}
                        bgImage={vpaCompany}
                        bgImageAlt="the dog"
                        strength={-200}
                    >
                        <div style={{ height: '400px' }} />
                    </Parallax>
                </div>
            </div>
        )
    }
}

class CompanyStatement extends Component {
    render() {
        const { classes } = this.props;
        return(
            <div className={classes.profile}>
                <div className = "image-wrapper">
                    <Parallax
                        className="profile-image"
                        blur={{ min: -30, max: 15 }}
                        bgImage={companyStatement}
                        bgImageAlt="the dog"
                        strength={-200}
                    >
                        <div style={{ height: '400px' }} />
                    </Parallax>
                </div>
            </div>
        )
    }
}

VPACompany.propTypes = {
    classes: PropTypes.object.isRequired,
}

CompanyStatement.propTypes = {
    classes: PropTypes.object.isRequired,
}
export default withStyles(styles)(VPACompany, CompanyStatement)