import React, { useState, useEffect } from 'react';
// CSS
import '../../css/util.css'
// MUI stuff
import { makeStyles } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
// Image
import logo from '../../assets/logos/logotrans.png'
import logo2 from '../../assets/logos/logos.png'

const useStyles = makeStyles((theme) => ({
    title: {
        flexGrow: 1,
        alignSelf: 'flex-end',
    },
}));

const GridNo = () => {
    const classes = useStyles();
    return(
        <Typography className={classes.title} variant="h5" noWrap>
            <div className={classes.logo}>
                <div className="logo-wrapper">
                    <img className="logo-image" src={logo} alt="logo" />
                </div>
            </div>
        </Typography>
    )
}

const VPD = () => {
    const classes = useStyles();
    return(
        <Typography className={classes.title} variant="h5" noWrap>
            <div className={classes.logo}>
                <div className="logo2-wrapper">
                    <img className="logo2-image" src={logo2} alt="logo" />
                </div>
            </div>
        </Typography>
    )
}

function Logo() {
    const BigOrSmall = useWindowWidth() >= 650 ? GridNo : VPD;
    return (
        <BigOrSmall />
    );
}

const useWindowWidth = () => {
    const [windowWidth, setWindowWidth] = useState(window.innerWidth);

    useEffect(() => {
        const handleWindowResize = () => {
            setWindowWidth(window.innerWidth);
        };
        window.addEventListener('resize', handleWindowResize);
        return () => window.removeEventListener('resize', handleWindowResize);
    }, []);
    return windowWidth
}

export default Logo