import React, { useState, useEffect } from 'react';
// CSS
import '../../css/util.css'
// MUI stuff
import { Grid } from '@material-ui/core'
import Paper from '@material-ui/core/Paper';
import GridList from '@material-ui/core/GridList';
import { makeStyles } from '@material-ui/core/styles';
import GridListTile from '@material-ui/core/GridListTile';
import GridListTileBar from '@material-ui/core/GridListTileBar';
// Part
import PartnerObed from './Pat/PartnerObed'
import PartnerOsei from './Pat/PartnerOsei'
import PartnerMark from './Pat/PartnerMark'
import PartnerKenneth from './Pat/PartnerKenneth'
// Image
import ObedImg from '../../assets/images/20190319_093102.jpg'
import OseiImg from '../../assets/images/20190319_093102.jpg'
import MarkImg from '../../assets/images/20190319_093102.jpg'
import KennethImg from '../../assets/images/20190319_093102.jpg'

const useStyles = makeStyles((theme) => ({
    gridHeader: {
        backgroundColor: '#00e1ff8c',
    },
    root: {
        display: 'flex',
        flexWrap: 'wrap',
        justifyContent: 'space-around',
        overflow: 'hidden',
        backgroundColor: theme.palette.background.paper,
    },
    gridList: {
        width: 1000,
        height: 500,
    },
    paper: {
        height:300,
        width: '100%',
    },
    grid: {
        display: 'flex',
        flexWrap: 'wrap',
        width: 1000,
    },
    icon: {
        color: 'rgba(255, 255, 255, 0.54)',
    },
}));

const GridNo = () => {
    const classes = useStyles();
    return(
        <Grid spacing={2} style={{ width: "100%" }} container  className={classes.grid}>
            <Grid item lg={6} sm={6} xs={12}>
                <GridListTile>
                    <Paper className={classes.paper}>
                        <img src={MarkImg} alt="Partner" />
                        <GridListTileBar
                            title="Mark Ofori Amanfo"
                            subtitle="Partner"
                            actionIcon={
                                <PartnerMark />
                            }
                        />
                    </Paper>
                </GridListTile>
            </Grid>
            <Grid item lg={6} sm={6} xs={12}>
                <GridListTile>
                    <Paper className={classes.paper}>
                        <img src={KennethImg} alt="Partner" />
                        <GridListTileBar
                            title="Dr. Kenneth Ofori-Boateng"
                            subtitle="Partner"
                            actionIcon={
                                <PartnerKenneth />
                            }
                        />
                    </Paper>
                </GridListTile>
            </Grid>
        </Grid>
    )
}

const VPD = () => {
    const classes = useStyles();
    return(
        <div className="VPD">
            <Grid spacing={2} style={{ width: "100%" }} container  className={classes.grid}>
                <Grid item lg={6} sm={6} xs={12}>
                    <GridListTile>
                        <Paper className={classes.paper}>
                            <img src={MarkImg} alt="Partner" />
                            <GridListTileBar
                                title="Mark Ofori Amanfo"
                                subtitle="Partner"
                                actionIcon={
                                    <PartnerMark />
                                }
                            />
                        </Paper>
                    </GridListTile>
                </Grid>
                <Grid item lg={6} sm={6} xs={12}>
                    <GridListTile>
                        <Paper className={classes.paper}>
                            <img src={KennethImg} alt="Partner" />
                            <GridListTileBar
                                title="Dr. Kenneth Ofori-Boateng"
                                subtitle="Partner"
                                actionIcon={
                                    <PartnerKenneth />
                                }
                            />
                        </Paper>
                    </GridListTile>
                </Grid>
            </Grid>
        </div>
    )
}

function TitlebarGridList() {
    const classes = useStyles();
    const BigOrSmall = useWindowWidth() >= 650 ? GridNo : VPD;
    return (
        <Grid className={classes.root}>
            <GridList cellHeight={280} className={classes.gridList}>
                {/* <GridListTile cols={2} style={{ height: 'auto' }}>
                <ListSubheader component="div"><h2><span className={classes.gridHeader}>Partners</span></h2></ListSubheader>
                </GridListTile> */}
                <Grid spacing={2} style={{ width: "100%" }} container  className={classes.grid}>
                    <Grid item lg={6} sm={6} xs={12}>
                        <GridListTile>
                            <Paper className={classes.paper}>
                                <img src={ObedImg} alt="Obed Owusu-Kissi" />
                                <GridListTileBar
                                    title="Obed Owusu-Kissi"
                                    subtitle="Senior Partner"
                                    actionIcon={
                                        <PartnerObed />
                                    }
                                />
                            </Paper>
                        </GridListTile>
                    </Grid>
                    <Grid item lg={6} sm={6} xs={12}>
                        <GridListTile>
                            <Paper className={classes.paper}>
                                <img src={OseiImg} alt="Partner" />
                                <GridListTileBar
                                    title="Osei Kwaku Adjaye-Gyamfi"
                                    subtitle="Senior Partner"
                                    actionIcon={
                                        <PartnerOsei />
                                    }
                                />
                            </Paper>
                        </GridListTile>
                    </Grid>
                </Grid>
                <BigOrSmall />
            </GridList>
        </Grid>
    );
}

const useWindowWidth = () => {
    const [windowWidth, setWindowWidth] = useState(window.innerWidth);

    useEffect(() => {
        const handleWindowResize = () => {
            setWindowWidth(window.innerWidth);
        };
        window.addEventListener('resize', handleWindowResize);
        return () => window.removeEventListener('resize', handleWindowResize);
    }, []);
    return windowWidth
}

export default TitlebarGridList