import React, { Component,  Fragment } from "react";
import PropTypes from 'prop-types'
import { withStyles, Container, Typography, Grid } from '@material-ui/core';
import AboutHeader from '../../util/headers/AboutHeader';
// import Fade from '../../util/about/Fade';, Modal, Backdrop, GridList, GridListTile, GridListTileBar, IconButton
import PartnerGrid from '../../util/about/PartnerGrid';
// import PartnerMark from "../../util/about/Pat/PartnerMark";
// import PartnerOsei from "../../util/about/Pat/PartnerOsei";
// import PartnerObed from "../../util/about/Pat/PartnerObed";
// import PartnerKenneth from "../../util/about/Pat/PartnerKenneth";
// import StarBorderIcon from '@material-ui/icons/StarBorder'
// import img from '../../assets/images/20190319_093102.jpg'

const styles = (theme) => ({
    aboutHeader: {
        backgroundColor: '#00e1ff8c',
    },
    span1: {
        color: 'red',
    },
})

class aboutUs extends Component {
    // state = {
    //     open: false
    // }
    // handleOpen = () => {
    //     this.setState({
    //         open: true
    //     })
    // }
    // handleClose = () => {
    //     this.setState({
    //         open: false
    //     })
    // }
    render() {
        const { classes } = this.props
        return(
            <Fragment>
                <AboutHeader />
                <br />
                <Container maxWidth="lg">
                <Typography component="div">
                    <Container>
                        <Grid
                            className={classes.root}
                            direction="row"
                            justify="space-evenly"
                            alignItems="center"
                            container
                        >
                            <Grid lg={1} sm={1} />
                            <Grid item lg={10} sm={10} xs={12}>
                                <Typography component="div">
                                    <p className={classes.p}>
                                        Virtual Point Associates is a firm with varied specialties and professional skills. We provide a balanced
                                        quality service for clients from a unique perspective, working with your team to establish the virtual point,
                                        the ideal vision, and mapping the road to make it a reality. This we do with full commitment to our basic
                                        tenets of professionalism - integrity, objectivity, independence and strict adherence to standards,
                                        applicable laws and regulations.
                                    </p>
                                </Typography>
                            </Grid>
                            <Grid lg={1} sm={1} />
                        </Grid>
                    </Container>
                </Typography>
                <br />
                <br />
                <Typography component="div">
                    <Container>
                        <Grid
                            className={classes.root}
                            direction="row"
                            justify="space-evenly"
                            alignItems="center"
                            container
                        >
                            <Grid lg={1} sm={1} />
                            <Grid item lg={10} sm={10} xs={12}>
                                <Typography variant="body1" component="h4">
                                    <h4><span className={classes.aboutHeader}>Team</span></h4>
                                </Typography>
                                <Typography variant="body2" component="div">
                                    <p className={classes.p}>
                                    Our team is headed by partners who are fellows of their professional associations.
                                    We are made up of experts with multi-industrial experience in financial services, aviation,
                                    manufacturing,  telecommunication, food and beverage, public sector, etc. With our result-oriented outlook,
                                    professional commitment, dint of hard work and perseverance, we make the virtual point a reality. We are particularly
                                    ‘fanatical’ about the success of our clients.
                                    </p>
                                </Typography>
                            </Grid>
                            <Grid lg={1} sm={1} />
                        </Grid>
                    </Container>
                </Typography>
                <br />
                <Typography component="div">
                    <Container>
                        <Grid
                            className={classes.root}
                            direction="row"
                            justify="space-evenly"
                            alignItems="center"
                            container
                        >
                            <Grid lg={1} sm={1} />
                            <Grid item lg={10} sm={10} xs={12}>
                                <Typography component="div">
                                    <p className={classes.p}>
                                        As a coherent team we have cross functional
                                        experience in financial services industry, manufacturing,
                                        aviation, public sector, telecommunication, food and beverage
                                        industry, with an excellent blend of both local and international exposure
                                    </p>
                                </Typography>
                            </Grid>
                            <Grid lg={1} sm={1} />
                        </Grid>
                    </Container>
                </Typography>
                <br />
                <Typography component="div">
                    <Container>
                        <Grid
                            className={classes.root}
                            direction="row"
                            alignItems="center"
                            container
                        >
                            <Grid lg={1} sm={1} />
                            {/* <Grid lg={10} sm={10} xs={12}> */}
                                <PartnerGrid />
                            {/* </Grid> */}
                            <Grid lg={1} sm={1} />
                        </Grid>
                    </Container>
                </Typography>
                <br />
                <br />
                <Typography component="div">
                    <Container>
                        <Grid
                            className={classes.root}
                            direction="row"
                            justify="space-evenly"
                            alignItems="center"
                            container
                        >
                            <Grid lg={1} sm={1} />
                            <Grid item lg={10} sm={10} xs={12}>
                                <Typography variant="body1" component="h4">
                                    <h4><span className={classes.aboutHeader}>Other Team Members</span></h4>
                                </Typography>
                                <Typography variant="body2" component="div">
                                    <p className={classes.p}>
                                        The firm in addition to the key team members has Fifteen <span className={classes.span1}>(</span>15<span className={classes.span1}>)</span> full time employees who have diverse
                                        expertise and qualifications. These employees have over the years been cultured into a dynamic support
                                        team who have imbibed the tenants of professionalism and are highly ethical. A number of people already
                                        in industry have also passed through our youth professional development system and are always willing
                                        to join teams at the least opportunity to offer their services.
                                    </p>
                                </Typography>
                            </Grid>
                            <Grid lg={1} sm={1} />
                        </Grid>
                    </Container>
                </Typography>
                <br />
                <br />
                <Typography component="div">
                    <Container>
                        <Grid
                            className={classes.root}
                            direction="row"
                            justify="space-evenly"
                            alignItems="center"
                            container
                        >
                            <Grid lg={1} sm={1} />
                            <Grid item lg={10} sm={10} xs={12}>
                                <Typography variant="body1" component="h4">
                                    <h4><span className={classes.aboutHeader}>Other Consultants and Affiliation</span></h4>
                                </Typography>
                                <Typography variant="body2" component="div">
                                    <p className={classes.p}>
                                        We also have a gamut of part-time consultants in economics, natural resource conservation, legal and IT 
                                        services. These are fifteen <span className={classes.span1}>(</span>15<span className={classes.span1}>)</span> on the roll at the moment and three of these mainly from academia are 
                                        PHDs in their fields of expertise. One of our consultants is also a certified fraud examiner and we fall on 
                                        his services as needed.
                                    </p>
                                </Typography>
                            </Grid>
                            <Grid lg={1} sm={1} />
                        </Grid>
                    </Container>
                </Typography>
                <br />
                <br />
                <Typography component="div">
                    <Container>
                        <Grid
                            className={classes.root}
                            direction="row"
                            justify="space-evenly"
                            alignItems="center"
                            container
                        >
                            <Grid lg={1} sm={1} />
                            <Grid item lg={10} sm={10} xs={12}>
                                <Typography variant="body1" component="h4">
                                    <h4><span className={classes.aboutHeader}>Capabilities/Values</span></h4>
                                </Typography>
                                <Typography variant="body2" component="div">
                                    <p className={classes.p}>
                                        It is important to indicate that the partners and key members of our team have long accounting and
                                        financial management experience in both local and multinational companies. It is worth further stating
                                        that our team is made up of professionals some of whom are bilingual and have working knowledge of
                                        Nigeria, Niger, The Gambia, the UK and the USA.
                                        Given our multi-industry, international experience and specific competence in taxation, accounting,
                                        auditing, special investigations, and general business consulting, we are more than poised to execute
                                        anyservice required of us both locally and internationally at the highest standard and most importantly at competitive terms.
                                    </p>
                                </Typography>
                            </Grid>
                            <Grid lg={1} sm={1} />
                        </Grid>
                    </Container>
                </Typography>
                </Container>
                <br />
                <br />
            </Fragment>
        )
    }
}
aboutUs.propTypes = {
    classes: PropTypes.object.isRequired,
}
export default withStyles(styles)(aboutUs)