import React, { Component, Fragment } from 'react';
import PropTypes from 'prop-types'
import ProjectsPage from '../../util/headers/ServiceHeader';
import { Container, Typography, Grid, withStyles, Card, CardContent } from '@material-ui/core';
import '../../css/service.css'

const styles = (theme) => ({
    profile: {
        '& .image-wrapper': {
            textAlign: 'center',
            position: 'relative',
        },
        '& .profile-image': {
            width: 400,
            height: 200,
            objectFit: 'cover',
            maxWidth: '100%',
            borderRadius: '20%'
        },
    },
    head: {
        textAlign:"center",
        paddingBottom: 60,
        '& hr': {
            maginBottom: '10px',
            border: 'none',
            width: '50%'
        },
    },
    contactContainer:{
        backgroundColor: '#fff',
        paddingBottom: 50
    },
    root: {
        minWidth: 275,
    },
    //card1:{
    //    paddingBottom: 40
    //},
    //card3:{
    //    paddingBottom: 23.8
    //},
    audit: {
        backgroundColor: '#00e1ff8c',
    },
    bullet: {
        display: 'inline-block',
        margin: '0 2px',
        transform: 'scale(0.8)',
    },
    title: {
        fontSize: 14,
    },
    pos: {
        marginBottom: 12,
    },
})

class service extends Component {
    render() {
        const { classes } = this.props;
        const bull = <span className={classes.bullet}>•</span>

        return (
            <Fragment>
                <ProjectsPage />
                <Container maxWidth='lg'>
                    <Typography style={{ textAlign: 'center'}} variant="body2">
                        <h4>VPA currently provides the following services</h4>
                    </Typography>
                    <Grid
                        className={classes.root}
                        direction="row"
                        justify="space-evenly"
                        alignItems="center"
                        container
                    >
                        <Grid item lg={5} sm={6} xs={12}>
                            <Card className={classes.card1}>
                                <CardContent>
                                    <Typography variant="h2">
                                        <span className={classes.audit}>Training</span>
                                    </Typography>
                                    <Typography variant="body2" component="p">
                                        <p>{bull} Team building and development</p>
                                        <p>{bull} Leadership and work place dispute resolution</p>
                                        <p>{bull} Internal control procedures</p>
                                        <p>{bull} Risk Management</p>
                                        <p>{bull} Accounting and internal control procedure for non-accountants</p>
                                    </Typography>
                                </CardContent>
                            </Card>
                        </Grid>
                        <Grid item lg={5} sm={6} xs={12}>
                            <Card className={classes.card3}>
                                <CardContent>
                                    <Typography variant="h2">
                                        <span className={classes.audit}>Taxation</span>
                                    </Typography>
                                    <Typography variant="body2" component="p">
                                        <p>{bull} Tax Compliance – VAT, CIT, Executive Tax, withholding taxes</p>
                                        <p>{bull} Tax planning, burden management and executive compensation strategies</p>
                                        <p>{bull} Tax avoidance initiatives and strategies</p>
                                        <p>{bull} Tax mediation and support</p>
                                        <p>{bull} Expatriate tax management</p>
                                    </Typography>
                                </CardContent>
                            </Card>
                        </Grid>
                    </Grid>
                    <Grid
                        className={classes.root}
                        direction="row"
                        justify="space-evenly"
                        alignItems="center"
                        container
                    >
                        <Grid item lg={5} sm={6} xs={12}>
                            <Card className={classes.card}>
                                <CardContent>
                                    <Typography variant="h2">
                                        <span className={classes.audit}>Accounting</span>
                                    </Typography>
                                    <Typography variant="body2" component="p">
                                        <p>{bull} Payroll Accounting – Total Outsourcing</p>
                                        <p>{bull} Back office Accounting - Total outsourcing</p>
                                        <p>{bull} Design and implantation of accounting systems for SMEs</p>
                                        <p>{bull} Financial accounting – systems design and implementation</p>
                                        <p>{bull} Financial Reporting – Statutory and Strategic Management</p>
                                        <p>{bull} Public sectors accounting – Design, control and implementation in line with statute</p>
                                    </Typography>
                                </CardContent>
                            </Card>
                        </Grid>
                        <Grid item lg={5} sm={6} xs={12}>
                            <Card className={classes.card4}>
                                <CardContent>
                                    <Typography variant="h2">
                                        <span className={classes.audit}>General Business Advisory</span>
                                    </Typography>
                                    <Typography variant="body2" component="p">
                                        <p>{bull} Design and implementation of business management systems</p>
                                        <p>{bull} Strategic planning and business management</p>
                                        <p>{bull} Business process design and management outsourcing</p>
                                        <p>{bull} Total quality management systems design and implementation</p>
                                        <p>{bull} Business registration, setup and regulatory compliance</p>
                                        <p>{bull} Market research, planning, feasibility studies, profile design and product introduction</p>
                                    </Typography>
                                </CardContent>
                            </Card>
                        </Grid>
                    </Grid>
                    <Grid
                        className={classes.root}
                        direction="row"
                        justify="space-evenly"
                        alignItems="center"
                        container
                    >
                        <Grid item lg={12} sm={12} xs={12}>
                            <Grid container>
                                <Grid item lg={4} sm={4}/>
                                <Grid item lg={4} sm={4} xs={12}>
                                    <Card className={classes.card5}>
                                        <CardContent className={classes.card1inner}>
                                            <Typography variant="h2">
                                                <span className={classes.audit}>Audit and Assurance</span>
                                            </Typography>
                                            <Typography variant="body2" component="p">
                                                <p>{bull} Forensic Audit</p>
                                                <p>{bull} Statutory Audit</p>
                                                <p>{bull} Systems quality assurance</p>
                                                <p>{bull} Internal audit outsourcing</p>
                                                <p>{bull} Management systems evaluation</p>
                                            </Typography>
                                        </CardContent>
                                    </Card>
                                </Grid>
                                <Grid item lg={4} sm={4}/>
                            </Grid>
                        </Grid>
                    </Grid>
                </Container>
                <br />
                <br />
            </Fragment>

        );
    }
}

service.propTypes = {
    classes: PropTypes.object.isRequired,
}
export default withStyles(styles)(service)