import React, {Component, Fragment} from 'react';
import PropTypes from 'prop-types'
import Fade from '../Fade';
import IconButton from '@material-ui/core/IconButton';
import InfoIcon from '@material-ui/icons/Info';
import CloseIcon from '@material-ui/icons/Close';
import Tooltip from '@material-ui/core/Tooltip';
import { Modal, Backdrop, withStyles } from '@material-ui/core';

const styles = (theme) => ({
    modal: {
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
        margin: theme.spacing(40, 5, 40),
    },
    paper: {
        backgroundColor: theme.palette.background.paper,
        border: '2px solid #000',
        boxShadow: theme.shadows[5],
        padding: theme.spacing(2, 4, 3),
    },
    icon: {
        color: 'rgba(255, 255, 255, 0.54)',
    },
    close: {
        color: '#009ffd',
        float: 'right'
    },
});

class PartnerObed extends Component {
    state = {
        open: false
    }
    handleOpen = () => {
        this.setState({
            open: true
        })
    }
    handleClose = () => {
        this.setState({
            open: false
        })
    }
    render() {
        const { classes } = this.props;
        return (
            <Fragment>
                <Tooltip title="info" placement="left-start">
                    <IconButton className={classes.icon} aria-label="Dr. Kenneth Ofori-Boateng" onClick={this.handleOpen}>
                        <InfoIcon className={classes.title} />
                    </IconButton>
                </Tooltip>
                <div className="part">
                    <Modal
                        aria-labelledby="spring-modal-title"
                        aria-describedby="spring-modal-description"
                        className={classes.modal}
                        open={this.state.open}
                        onClose={this.handleClose}
                        closeAfterTransition
                        BackdropComponent={Backdrop}
                        BackdropProps={{
                            timeout: 500
                        }}
                    >
                        <Fade in={this.state.open}>
                            <div className={classes.paper}>
                                <IconButton className={classes.close} onClick={this.handleClose}>
                                    <CloseIcon className={classes.ex} />
                                </IconButton>
                                <h2 id="spring-modal-title">Obed Owusu-Kissi (MBA, ICAG, CIT)</h2>
                                <p id="spring-modal-description">
                                    Senior Partner (Business Advisory and Taxation), is well versed
                                    in corporate-wide total quality management in aviation, manufacturing and service environs, and has
                                    extensive experience in the design and implementation of strategic management systems using cross-
                                    functional teams, financial systems and internal controls, business set-up, change management, human
                                    resources management, taxation and quality assurance. His background includes international corporate
                                    exposure in developed and developing economies. He received his BSc. (Accounting Option) and MBA in
                                    Finance from the University of Ghana. He is also a member of the Institute of Chartered Accountants
                                    Ghana and the Chartered Institute of Taxation Ghana.
                                    Obed has held senior management positions in local and international organizations such as Ghana Civil
                                    Aviation Authority, Ghana Airways, North American Airlines, Kasapreko Company Limited, and others.
                                </p>
                            </div>
                        </Fade>
                    </Modal>
                </div>
            </Fragment>
        )
    }
}

PartnerObed.propTypes = {
    classes: PropTypes.object.isRequired,
}
export default withStyles(styles)(PartnerObed)
