import React from 'react';
import { Parallax } from 'react-parallax';
import image from '../assets/images/luxepar.jpg'
import { makeStyles } from '@material-ui/core';

const useStyle = makeStyles((theme) => ({
}))

const MyComponent = () =>{
    const classes = useStyle()
    return(
        <div className={classes.ParallaxCom} style={{paddingTop: "20px"}}>
            <div className="parallaxHome">
                <Parallax
                    blur={{ min: -15, max: 15 }}
                    bgImage={image}
                    bgImageAlt="the dog"
                    strength={-200}
                >
                    <div className="parallaxHeight" />
                </Parallax>
            </div>
        </div>
    );
}
export default MyComponent;