import React, { useState, useEffect } from 'react';
// CSS
import '../../css/util.css'
// MUI stuff
import { makeStyles } from '@material-ui/core/styles';
// Image
import image from '../../assets/images/Original-Hero.jpg'
import image1 from '../../assets/images/20190319_093102.jpg'
// react parallax
import { Parallax } from 'react-parallax';

const useStyles = makeStyles((theme) => ({
    parallax: {
        height: 440,
        top: 0
    }
}));

const GridNo = () => {
    const classes = useStyles();
    return(
        <div style={{paddingTop: "128px"}}>
            <div className="parallaxService">
                <Parallax
                bgImage={image}
                strength={-200}
                bgImageAlt="Team"
                blur={{ min: -65, max: 15 }}
                className={classes.parallax}
                >
                    <div />
                </Parallax>
            </div>
        </div>
    )
}

const VPD = () => {
    const classes = useStyles();
    return(
        <div style={{paddingTop: "128px"}}>
            <div className="parallaxService2">
                <Parallax
                bgImage={image1}
                strength={-200}
                bgImageAlt="Team"
                blur={{ min: -65, max: 15 }}
                className={classes.parallax}
                >
                    <div />
                </Parallax>
            </div>
        </div>
    )
}

function ServiceHeader() {
    const BigOrSmall = useWindowWidth() >= 650 ? GridNo : VPD;
    return (
        <BigOrSmall />
    );
}

const useWindowWidth = () => {
    const [windowWidth, setWindowWidth] = useState(window.innerWidth);

    useEffect(() => {
        const handleWindowResize = () => {
            setWindowWidth(window.innerWidth);
        };
        window.addEventListener('resize', handleWindowResize);
        return () => window.removeEventListener('resize', handleWindowResize);
    }, []);
    return windowWidth
}

export default ServiceHeader