import React, {Component, Fragment} from 'react';
import PropTypes from 'prop-types'
import Fade from '../Fade';
import IconButton from '@material-ui/core/IconButton';
import InfoIcon from '@material-ui/icons/Info';
import CloseIcon from '@material-ui/icons/Close';
import Tooltip from '@material-ui/core/Tooltip';
import { Modal, Backdrop, withStyles } from '@material-ui/core';

const styles = (theme) => ({
    modal: {
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
        margin: theme.spacing(40, 5, 40),
    },
    paper: {
        backgroundColor: theme.palette.background.paper,
        border: '2px solid #000',
        boxShadow: theme.shadows[5],
        padding: theme.spacing(2, 4, 3),
    },
    icon: {
        color: 'rgba(255, 255, 255, 0.54)',
    },
    close: {
        color: '#009ffd',
        float: 'right'
    },
});

class PartnerMark extends Component {
    state = {
        open: false
    }
    handleOpen = () => {
        this.setState({
            open: true
        })
    }
    handleClose = () => {
        this.setState({
            open: false
        })
    }
    render() {
        const { classes } = this.props;
        return (
            <Fragment>
                <Tooltip title="info" placement="left-start">
                    <IconButton className={classes.icon} aria-label="Dr. Kenneth Ofori-Boateng" onClick={this.handleOpen}>
                        <InfoIcon className={classes.title} />
                    </IconButton>
                </Tooltip>
                <div className="part">
                    <Modal
                        aria-labelledby="spring-modal-title"
                        aria-describedby="spring-modal-description"
                        className={classes.modal}
                        open={this.state.open}
                        onClose={this.handleClose}
                        closeAfterTransition
                        BackdropComponent={Backdrop}
                        BackdropProps={{
                            timeout: 500
                        }}
                    >
                        <Fade in={this.state.open}>
                            <div className={classes.paper}>
                                <IconButton className={classes.close} onClick={this.handleClose}>
                                    <CloseIcon className={classes.ex} />
                                </IconButton>
                                <h2 id="spring-modal-title">Mark Ofori Amanfo (BSC., MBA, FCCA)</h2>
                                <p id="spring-modal-description">
                                    Partner (Advisory, Auditing and Management of the firm)
                                    specializes in quality systems management and internal controls. Business valuation and due diligence,
                                    quality system assurance, internal auditing, forensic auditing, contract negotiations, regulatory
                                    negotiations, back office accounting, systems implementation, and project management are specific areas
                                    of expertise acquired from working with manufacturing and services companies, in aviation and with
                                    international organizations. He has working knowledge of the business and operating environment of
                                    European and American countries as well as most West African countries including Nigeria, Ghana, The
                                    Gambia, Senegal, Guinea (Conakry) and Ethiopia. Mark received his BSc. and MBA in accounting from the
                                    University of Ghana Business School. He is a Fellow of the ACCA, a member of the Institute of Chartered
                                    Accountants Ghana (ICAG) and a member of the Institute of Internal Auditors (IIA) Ghana.
                                    Mark has served several board committees and was on numerous executive delegations to different
                                    countries for negotiations. His prior experiences were with Gold Coast Motors, Unilever Ghana, Meridian
                                    Tobacco Company, Pioneer Tobacco Company and volunteered at the Village Water Project in Wenchi in the Brong Ahafo Region.
                                </p>
                            </div>
                        </Fade>
                    </Modal>
                </div>
            </Fragment>
        )
    }
}

PartnerMark.propTypes = {
    classes: PropTypes.object.isRequired,
}
export default withStyles(styles)(PartnerMark)