import React from "react";
// CSS
import '../css/carousel.css'
// Images
import firstSlide from '../assets/images/img11.jpg'
import secondSlide from '../assets/images/img12.jpg'
import thirdSlide from '../assets/images/img13.jpg'
// MUI Stuff
import { makeStyles } from '@material-ui/core/styles';
// MDB Stuff
import { MDBCarousel, MDBCarouselInner, MDBCarouselItem, MDBView, MDBContainer } from 'mdbreact';

const useStyles = makeStyles((theme) => ({
    mdbCarousel: {
        display: 'flex',
        alignItems: 'center',
        padding: theme.spacing(0, 0),
        justifyContent: 'flex-end',
        width: '100%',
    }
}))

const CarouselPage = () => {
    const classes = useStyles()
    return (
        <MDBContainer className={classes.mdbCarousel}>
            <MDBCarousel
                activeItem={1}
                length={3}
                showControls={true}
                showIndicators={false}
                className="z-depth-1"
                slide
            >
                <MDBCarouselInner>
                    <MDBCarouselItem itemId="1">
                        <MDBView>
                            <img
                                className="d-block w-100"
                                src={firstSlide}
                                alt="First slide"
                            />
                        </MDBView>
                    </MDBCarouselItem>
                    <MDBCarouselItem itemId="2">
                        <MDBView>
                            <img
                                className="d-block w-100"
                                src={secondSlide}
                                alt="Second slide"
                            />
                        </MDBView>
                    </MDBCarouselItem>
                    <MDBCarouselItem itemId="3">
                        <MDBView>
                            <img
                                className="d-block w-100"
                                src={thirdSlide}
                                alt="Third slide"
                            />
                        </MDBView>
                    </MDBCarouselItem>
                </MDBCarouselInner>
            </MDBCarousel>
        </MDBContainer>
    );
}

export default CarouselPage;