import React, {Component, Fragment} from 'react';
import PropTypes from 'prop-types'
import Fade from '../Fade';
import IconButton from '@material-ui/core/IconButton';
import InfoIcon from '@material-ui/icons/Info';
import CloseIcon from '@material-ui/icons/Close';
import Tooltip from '@material-ui/core/Tooltip';
import { Modal, Backdrop, withStyles, Container } from '@material-ui/core';

const styles = (theme) => ({
    modal: {
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
        margin: theme.spacing(40, 5, 40),
    },
    paper: {
        backgroundColor: theme.palette.background.paper,
        border: '2px solid #000',
        boxShadow: theme.shadows[5],
        padding: theme.spacing(2, 4, 3),
    },
    icon: {
        color: 'rgba(255, 255, 255, 0.54)',
    },
    close: {
        color: '#009ffd',
        float: 'right'
    },
});

class PartnerKenneth extends Component {
    state = {
        open: false
    }
    handleOpen = () => {
        this.setState({
            open: true
        })
    }
    handleClose = () => {
        this.setState({
            open: false
        })
    }
    render() {
        const { classes } = this.props;
        return (
            <Fragment>
                <Tooltip title="info" placement="left-start">
                    <IconButton className={classes.icon} aria-label="Dr. Kenneth Ofori-Boateng" onClick={this.handleOpen}>
                        <InfoIcon className={classes.title} />
                    </IconButton>
                </Tooltip>
                    <Container>
                        <div className="part">
                            <Modal
                                aria-labelledby="spring-modal-title"
                                aria-describedby="spring-modal-description"
                                className={classes.modal}
                                open={this.state.open}
                                onClose={this.handleClose}
                                closeAfterTransition
                                BackdropComponent={Backdrop}
                                BackdropProps={{
                                    timeout: 500
                                }}
                            >
                                <Fade in={this.state.open}>
                                    <div className={classes.paper}>
                                        <IconButton className={classes.close} onClick={this.handleClose}>
                                            <CloseIcon className={classes.ex} />
                                        </IconButton>
                                        <h2 id="spring-modal-title">Dr. Kenneth Ofori-Boateng (Adjunct Consultant)</h2>
                                        <p id="spring-modal-description">
                                            Kenneth is an adjunct consultant of Virtual Point Associates who handles economic policy matters,
                                            development research and consulting in general. He has been associated with the firm since he completed
                                            his PHD from the University of Ibadan. His primary occupation is lecturing at the Ghana Institute of Public
                                            Administration (GIMPA) - where he teaches managerial economics at the Business School. His academic
                                            laurels are in the area of monetary economics, climate change and environmental economics, and poverty
                                            studies. He received his BSc. in Economics and Law as well as his MA in econometrics from the Kwame
                                            Nkrumah University of Science and Technology (KNUST) in Kumasi. He has several publications to his credit
                                            and has peer reviewed a number of them
                                        </p>
                                    </div>
                                </Fade>
                            </Modal>
                        </div>
                    </Container>
            </Fragment>
        )
    }
}

PartnerKenneth.propTypes = {
    classes: PropTypes.object.isRequired,
}
export default withStyles(styles)(PartnerKenneth)