import React from 'react';
import clsx from 'clsx';
import logo from '../../assets/logos/logotrans.png'
import Logo from '../../util/nav/logo'
import { Link } from 'react-router-dom'
import { makeStyles } from '@material-ui/core/styles';
import Drawer from '@material-ui/core/Drawer';
import CssBaseline from '@material-ui/core/CssBaseline';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import List from '@material-ui/core/List';
import Typography from '@material-ui/core/Typography';
import Divider from '@material-ui/core/Divider';
import IconButton from '@material-ui/core/IconButton';
import MenuIcon from '@material-ui/icons/Menu';
import CloseIcon from '@material-ui/icons/Close';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import HomeIcon from '@material-ui/icons/Home';
import GroupIcon from '@material-ui/icons/Group';
import SignalCellular4BarIcon from '@material-ui/icons/SignalCellular4Bar';
import ChromeReaderModeIcon from '@material-ui/icons/ChromeReaderMode';
import PetsIcon from '@material-ui/icons/Pets';
import SearchIcon from '@material-ui/icons/Search';

const drawerWidth = 240;

const useStyles = makeStyles((theme) => ({
    root: {
    display: 'flex',
    },
    appBar: {
        transition: theme.transitions.create(['margin', 'width'], {
        easing: theme.transitions.easing.sharp,
        duration: theme.transitions.duration.leavingScreen,
    }),
    },
//    logo: {
//        '& .logo-wrapper': {
//            textAlign: 'center',
//            position: 'relative',
//        },
//        '& .logo-image': {
//            width: '100%',
//            height: 200,
//            objectFit: 'cover',
//            maxWidth: '100%',
//            borderRadius: '20%',
//            textAlign: 'center',
//        },
//    },
    appBarShift: {
        width: `calc(100% - ${drawerWidth}px)`,
        marginLeft: drawerWidth,
        transition: theme.transitions.create(['margin', 'width'], {
            easing: theme.transitions.easing.easeOut,
            duration: theme.transitions.duration.enteringScreen,
        }),
    },
    menuButton: {
        marginRight: theme.spacing(2),
    },
    hide: {
        display: 'none',
    },
    drawer: {
        width: drawerWidth,
        flexShrink: 0,
    },
    drawerPaper: {
        width: drawerWidth,
    },
    drawerHeader: {
        display: 'flex',
        alignItems: 'center',
        padding: theme.spacing(0, 1),
        // necessary for content to be below app bar
        ...theme.mixins.toolbar,
        justifyContent: 'flex-end',
    },
    drawerText: {
        display: 'flex',
        alignItems: 'center',
        padding: theme.spacing(0, 3),
        // necessary for content to be below app bar
        ...theme.mixins.toolbar,
    },
    toolbar: {
        minHeight: 128,
        alignItems: 'flex-start',
        paddingTop: 8,
        paddingBottom: 16,
    },
    content: {
        flexGrow: 1,
        padding: theme.spacing(3),
        transition: theme.transitions.create('margin', {
            easing: theme.transitions.easing.sharp,
            duration: theme.transitions.duration.leavingScreen,
        }),
        marginLeft: -drawerWidth,
    },
    contentShift: {
        transition: theme.transitions.create('margin', {
            easing: theme.transitions.easing.easeOut,
            duration: theme.transitions.duration.enteringScreen,
        }),
        marginLeft: 0,
    },
    title: {
        flexGrow: 1,
        alignSelf: 'flex-end',
    },
}));

export default function NavBar() {
    const classes = useStyles();
    //const theme = useTheme();
    const [open, setOpen] = React.useState(false);

    const handleDrawerOpen = () => {
        setOpen(true);
    };

    const handleDrawerClose = () => {
        setOpen(false);
    };

    return (
        <div className={classes.root}>
            <CssBaseline />
            <AppBar
                position="fixed"
                className={clsx(classes.appBar, {
                    [classes.appBarShift]: open,
                })}
            >
                <Toolbar className={classes.toolbar}>
                    <IconButton
                        color="inherit"
                        aria-label="open drawer"
                        onClick={handleDrawerOpen}
                        onKeyDown={handleDrawerClose}
                        edge="start"
                        className={clsx(classes.menuButton, open && classes.hide)}
                    >
                        <MenuIcon />
                    </IconButton>
                    <Logo />
                    <IconButton aria-label="search" color="inherit">
                        <SearchIcon />
                    </IconButton>
                </Toolbar>
            </AppBar>
            <Drawer
                className={classes.drawer}
                variant="persistent"
                anchor="left"
                open={open}
                onClick={handleDrawerClose}
                classes={{
                    paper: classes.drawerPaper,
                }}
            >
                <div className={classes.drawerHeader}>
                    <IconButton onClick={handleDrawerClose}>
                        <CloseIcon />
                    </IconButton>
                </div>
                <Divider />
                <List>
                    <ListItem component={Link} to="/" button>
                        <ListItemIcon> <HomeIcon style={{ fontSize: 40}} /> </ListItemIcon>
                        <ListItemText>Home</ListItemText>
                    </ListItem>
                </List>
                <List>
                    <ListItem component={Link} to="/service" button>
                        <ListItemIcon> <SignalCellular4BarIcon style={{ fontSize: 40}} /> </ListItemIcon>
                        <ListItemText>Services</ListItemText>
                    </ListItem>
                </List>
                <List>
                    <ListItem component={Link} to="/aboutUs" button>
                        <ListItemIcon> <GroupIcon style={{ fontSize: 40}} /> </ListItemIcon>
                        <ListItemText>About Us</ListItemText>
                    </ListItem>
                </List>
                <List>
                    <ListItem component={Link} to="/careers" button>
                        <ListItemIcon> <PetsIcon style={{ fontSize: 40}} /> </ListItemIcon>
                        <ListItemText>Careers</ListItemText>
                    </ListItem>
                </List>
                <List>
                    <ListItem component={Link} to="/publications" button>
                        <ListItemIcon> <ChromeReaderModeIcon style={{ fontSize: 40}} /> </ListItemIcon>
                        <ListItemText>Publications</ListItemText>
                    </ListItem>
                </List>
                <Divider />
                <List>
                <Typography className={classes.title} variant="h5" noWrap>
                    <div className={classes.navlogo}>
                        <div className="navlogo-wrapper">
                            <img className="navlogo-image" src={logo} alt="logo" />
                        </div>
                    </div>
                </Typography>
                </List>
            </Drawer>
        </div>
    );
}